  background: none;
}

.glow-on-hover:hover:before {
  opacity: 1;
}

.glow-on-hover:after {
  z-index: -1;
  content: "";
  width: 100%;
  height: 100%;
  background: #111;
  border-radius: 10px;
  position: absolute;
  top: 0;
  left: 0;
}

